// src/updateBlocksComponents.ts
import { randomUUID } from "node:crypto";
import {
  ComponentType as ComponentType8,
  PanelContentType as PanelContentType4
} from "@wix/ambassador-devcenter-apps-v1-app-old/types";
var updateBlocksComponents = (manifest, blocksComponents) => {
  if (!blocksComponents?.length) {
    return manifest;
  }
  const mainComponentFromBlocks = blocksComponents.find(
    (blocksComponent) => blocksComponent.type === ComponentType8.STUDIO
  );
  const dataComponentFromBlocks = blocksComponents.find(
    (blocksComponent) => blocksComponent.type === ComponentType8.DATA_COMPONENT
  );
  const updatedExtensions = manifest.components.map((comp) => {
    if (comp.compType === ComponentType8.STUDIO) {
      return {
        ...comp,
        compId: mainComponentFromBlocks.id,
        compData: {
          studioComponentData: {
            ...mainComponentFromBlocks.data.studioComponentData
          }
        }
      };
    }
    const componentDataFromBlocks = blocksComponents.find(
      (component) => component.id === comp.compId
    );
    if (!componentDataFromBlocks) {
      return comp;
    }
    if (comp.compType === ComponentType8.STUDIO_WIDGET) {
      return {
        ...comp,
        compName: componentDataFromBlocks.name,
        compData: {
          studioWidgetComponentData: {
            ...componentDataFromBlocks.data.studioWidgetComponentData,
            widgetBundleUrl: comp.compData.studioWidgetComponentData.widgetBundleUrl
          }
        }
      };
    }
    if (comp.compType === ComponentType8.PANEL && comp.compData.panel.contentType === PanelContentType4.url) {
      const codeBundleUrl = new URL(comp.compData.panel.url).searchParams.get(
        "codeBundleUrl"
      );
      const panelUrl = new URL(componentDataFromBlocks.data.panel.url);
      panelUrl.searchParams.set("codeBundleUrl", codeBundleUrl ?? "");
      return {
        ...comp,
        compName: componentDataFromBlocks.name,
        compData: {
          panel: {
            ...componentDataFromBlocks.data.panel,
            url: panelUrl.href,
            contentType: comp.compData.panel.contentType
          }
        }
      };
    }
    return comp;
  });
  if (dataComponentFromBlocks) {
    const dataComponent = {
      compId: dataComponentFromBlocks.id ?? randomUUID(),
      compData: {
        dataComponent: {
          ...dataComponentFromBlocks.data.dataComponent
        }
      },
      compType: ComponentType8.DATA_COMPONENT
    };
    updatedExtensions.push(dataComponent);
  }
  return { ...manifest, components: updatedExtensions };
};

